.ListaTareasSeccion {
  display: flex;
  justify-content: space-around;
  padding: 1rem;
  width: 100%;
  text-align: center;
  transition: all 0.2s ease-in-out;
  height: 1.5rem;
  border-bottom: 2px solid #b8f3ff;
  background-color: #000000;
}
.ListaTareasSeccion h1 {
  margin: 0;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}
.ListaTareasSeccion > button {
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
