.ListaDeUsuarios {
  display: flex;
  flex-direction: column;
  align-items: center;
  place-content: center;
  max-height: 100%;
  height: min-content;
  width: 100%;
}
.ListaDeUsuarios-titulo {
  margin: 2rem;
  padding: 1rem;
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
  width: 100%;
}
.CargandoUsuarios {
  display: flex;
  justify-content: top;
  width: 100px;
  height: 100px;
  border-bottom: 2px solid #fff;
  animation: cargandoUsuarios 0.2s linear infinite;
}

@keyframes cargandoUsuarios {
  0% {
    transform: translate(-200%);
  }
  100% {
    transform: translate(200%);
  }
}
